@import '../../../node_modules/bootstrap/scss/bootstrap-grid.scss';
@import '../../../node_modules/@fortawesome/fontawesome-free/css/all.min.css';

.title{
	border-bottom:1px solid #6153CC;
	display: flex;
	align-items: center;
	a{
		margin-right:10px;
		font-size:20px;
	}
	h1{
		font-size: 27px;
		margin: 10px 0;
	}
}
.details{
	display: flex;
	align-items: center;
	margin: 10px 0;
	flex-wrap: wrap;
	.detail{
		margin-right: 20px;
		i{
			color:#6153CC;
		}
	}
}
.slider-wrapper{
	position: relative;
	
	.inner-slider{
		img{
			max-width: 100%;
			height:400px;
			width:100%;
			object-fit:cover;
		}
	}
	.controls{
		display: flex;
		width: 100%;
		justify-content: space-between;
		position: absolute;
		top: 40%;
		div{
			background-color: white;
			padding: 10px;
			color: orchid;
			font-weight: 800;
			box-shadow: 3px 0px 15px 1px #bbb;
			cursor: pointer;
		}
	}
	.stars{
		position: absolute;
		bottom:10%;
		padding-left: 10px;
		font-size: 15px;
		color: white;

		i.gold {
			color: gold;
		}
	}
}
.params{
	display: flex;
	justify-content: space-between;
	color: #555;
}
.capacity{
	color: #444;
	font-size: 14px;
	margin:10px 0;
}
.sport-tags{
	span{
		background-color:grey;
		color: white;
		display: inline-block;
		margin-right: 5px;
		border-radius: 15px;
		padding:7px;
		font-size: 13px;
		font-family: sans-serif;
	}
}
.attrs{
	padding: 0;
	list-style: none;
	li{
		display: flex;
		width:100%;
		justify-content: space-between;
		margin:10px 0;
		i{
			color:forestgreen;
		}
	}
}
.content{
	padding:40px 0;
	font-family: sans-serif;
	font-size: 15px;
	background-color: #eee;
}
.payment{
	padding:100px 0;
	padding-right: 40px;
}
.payment p{
	padding-right: 40px;
}
.payment img{
	max-width: 100%;
}
@media (max-width: 768px){
	.payment{
		text-align: center;
	}
}